<template>
  <div id="sitcon-map">
    <div :style="{ width: width, height: height }"></div>
    <div class="sitetext">中央研究院人文社會科學館</div>
  </div>
</template>

<script>
import 'ol/ol.css'
import Map from 'ol/map'
import Feature from 'ol/feature'
import Point from 'ol/geom/point'
import Style from 'ol/style/style'
import Icon from 'ol/style/icon'
import LayerVector from 'ol/layer/vector'
import SourceVector from 'ol/source/vector'
import Interaction from 'ol/interaction'
import Tile from 'ol/layer/tile'
import OSM from 'ol/source/osm'
import View from 'ol/view'
import Proj from 'ol/proj'
export default {
  name: 'SitconMap',
  props: {
    width: {
      type: String,
      default: 320 + 'px'
    },
    height: {
      type: String,
      default: 240 + 'px'
    }
  },
  data () {
    return {
      osmMap: null
    }
  },
  methods: {
    mapMarkerLayer (name, imgSrc, pos) {
      var icon = new Feature({
        name: name,
        geometry: new Point(Proj.fromLonLat(pos))
      })
      var iconStyle = new Style({
        image: new Icon({
          scale: 0.5,
          anchor: [0.5, 1],
          anchorXUnits: 'fraction',
          anchorYUnits: 'fraction',
          src: imgSrc
        })
      })
      icon.setStyle(iconStyle)
      return new LayerVector({
        source: new SourceVector({ features: [icon] }),
        style: iconStyle
      })
    },
    makeMap () {
      const iconMain = this.mapMarkerLayer('main', '/2019/static/img/sitcon-logo.png', [121.6116, 25.0410])
      const osmMap = new Map({
        target: this.$el.firstChild,
        interactions: Interaction.defaults({mouseWheelZoom: false}),
        layers: [
          new Tile({
            source: new OSM(),
            opacity: 0.6
          }),
          iconMain
        ],
        view: new View({
          center: Proj.fromLonLat([121.6116, 25.0410]),
          zoom: 16
        })
      })
      return osmMap
    }
  },
  mounted () {
    this.osmMap = this.makeMap()
  }
}
</script>

<style lang="scss" scoped>
#sitcon-map {
  position: relative;
  width: calc(100% - 140px * 2);
  padding: 64px 140px;

  @media screen and (max-width: 900px) {
    width: 100%;
    padding: 0;
    padding-top: 4rem;
  }

  .sitetext {
    text-align: center;
    position: absolute;
    bottom: 20%;
    border: 1px solid black;
    left: 50%;
    transform: translate(-50%);
    color: black;
    padding: 12px;
    font-size: 16px;
    background-color: rgba(255, 255, 255, 0.7);
    line-height: 1.4;
    font-family: 'Noto Sans CTK TC', sans-serif;
  }
}
</style>



